  font-weight: 700;
  color: #d57a46;
  text-align: center;
  text-transform: uppercase;
  font-size: 28px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 320px;
}
@media screen and (orientation: landscape) {
  .hero .lead {
    transform: translate(-50%, -75%);
  }
