}
@media screen and (min-width: 990px) {
  .hero .lead {
    font-size: 60px;
    width: 740px;
  }
}
.hero .animation-wrapper {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: auto;
  left: 50%;
  bottom: 0;
  overflow: hidden;
  transform: translateX(-50%);
}
