  left: 58%;
  width: 40%;
  animation-delay: 650ms;
}
.hero .animation-wrapper .ss2 {
  top: 12px;
  left: 50%;
  margin-left: -47%;
  width: 40%;
  animation-delay: 450ms;
}
@keyframes heroIntro {
  0% {
    transform: translateY(50%);
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
